// import { Link, useLocation } from "react-router-dom";
import Aos from "aos";
import { Link } from "react-router-dom";
// import headlogo from "../../containers/HomeDemo4/MyLogoHeader.png"
import headlogo from "../../../containers/HomeDemo4/MyLogoHeader.png";

const SecHeader = () => {
  Aos.init();

  const dropDown = [
    {
      path: "/nftNode",
      nameLink: "Wizz Nodes",
    },
    {
      path: "/nftairtickets",
      nameLink: "Wizz Air Tickets",
    },
  ];

  return (
    <nav className="navbar navbar-expand-lg navbar-white fixed-top" id="banner">
      <div className="container">
        {/* Brand */}
        <Link className="navbar-brand" to="/">
          <img src={headlogo} alt="Logo" width="70" draggable="false" />
        </Link>
        {/* Toggler/collapsibe Button */}
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" />
        </button>

        {/* Navbar links */}
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto">
            <li className="nav-item">
              <Link className="nav-link" to="/">
                Home
              </Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link" to="aboutus">
                About Us
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="ecosystem">
                Eco System
              </Link>
            </li>
            <li className="nav-item dropdown">
              <Link
                className="nav-link dropdown-toggle"
                id="navbarDropdown"
                role="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                Products
              </Link>
              <div className="dropdown-menu">
                {dropDown.map((item, key) => (
                  <Link key={key} className="dropdown-item" to={item.path}>
                    {item.nameLink}
                  </Link>
                ))}
              </div>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/roadmap">
                Roadmap
              </Link>
            </li>
            {/* <li className="nav-item">
              <a className="nav-link" href="#blog">
                Blog
              </a>
            </li> */}
            <li className="ml-3 nav">
              <div className="d-flex justify-content-center align-items-center">
                <div className="bot-left1 rounded-pill">
                  <a
                    href="https://app.wizzecosystem.com"
                    target="_blank"
                    className="btn golden px-4 py-1"
                  >
                    {/* <span className="test"> */}
                    {/* <span className="golden"> */}
                    <span>App</span>
                    {/* </span> */}
                    {/* </span> */}
                  </a>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default SecHeader;
