import congo from "../../assets/img/congratulation/Congratulations.gif";

const Congratulation = () => {
  return (
    <>
      <div className="d-flex justify-content-center align-items-center mt-4">
        <div className="bot-left1 rounded-pill">
          <a
            href="https://app.wizzecosystem.com"
            target="_blank"
            className="btn golden px-4 py-1"
          >
            <span>App</span>
          </a>
        </div>
      </div>
      <div className="d-flex justify-content-center align-items-center">
        <img
          src={congo}
          alt="congratulation"
          style={{ height: "800px", marginTop: "50px", borderRadius: "20px" }}
        />
      </div>
    </>
  );
};

export default Congratulation;
