import React, { useEffect } from "react";
import { Helmet } from "react-helmet";
import { Routes, Route } from "react-router-dom";
import Aos from "aos";

import HomeDemo4 from "./pages/HomeDemo4";

import "aos/dist/aos.css";
import "./assets/css/General.css";
import "./assets/css/bootstrap.min.css";
import "./assets/css/responsive.css";

import "bootstrap/dist/js/bootstrap.bundle.min";
import Login from "./components/Login";
import SignUp from "./components/SignUp";
import ForgetPassword from "./components/ForgetPassword";
import ForgetPasswordViaEmail from "./components/ForgetPasswordViaEmail";
import Sidebar from "./components/Sidebar";
import Congratulation from "./components/congratulation/Congratulation";
import AboutUs from "./components/AboutUs";
import OurServices from "./containers/HomeDemo4/OurServices/index";
import Roadmap from "./components/Roadmap";
import Navbar from "./layouts/Header/SecHeader";
import Ournodes from "./containers/HomeDemo4/Ournodes";
import AirTickets from "./containers/HomeDemo4/AirTickets/AirTickets";
import useScrollToTop from "./layouts/useScrollToTop/useScrollToTop";
import ModalComponent from "./layouts/Modal/Modal";
// D:\wizzzz\Wizz-Token\src\layouts\Header\SecHeader\index.jsx

// D:\wizzzz\Wizz-Token\src\components\Roadmap\index.jsx

const App = () => {
  useEffect(() => {
    Aos.init({
      duration: 1000,
    });
  }, []);

  useScrollToTop();

  return (
    <div className="App">
      <Helmet>
        <meta charset="utf-8" />
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1, shrink-to-fit=no"
        />
        <title>Wizz Ecosystem</title>
        <link
          href="https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700"
          rel="stylesheet"
        />
        <link
          rel="stylesheet"
          href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"
          integrity="sha256-eZrrJcwDc/3uDhsdt61sL2oOBY362qM3lon1gyExkL0="
          crossorigin="anonymous"
        />
      </Helmet>
      <Navbar />
      {/* <ModalComponent /> */}
      {/* <Sidebar /> */}
      <Routes>
        <Route exact path="/" element={<HomeDemo4 />} />
        <Route exact path="/aboutus" element={<AboutUs />} />
        <Route exact path="/ecosystem" element={<OurServices />} />
        <Route exact path="/nftNode" element={<Ournodes />} />
        <Route exact path="/nftairtickets" element={<AirTickets />} />
        <Route exact path="/roadmap" element={<Roadmap />} />
        <Route exact path="/index-demo-4/login" element={<Login />} />
        <Route
          exact
          path="/index-demo-4/forgetpassword"
          element={<ForgetPassword />}
        />
        <Route
          exact
          path="/index-demo-4/forgetpasswordviaemail"
          element={<ForgetPasswordViaEmail />}
        />
        <Route exact path="/index-demo-4/signup" element={<SignUp />} />
        <Route exact path="/congratulation" element={<Congratulation />} />
      </Routes>
    </div>
  );
};

export default App;

