import SectionHeading from "../SectionHeading";
import { VscCircleFilled } from "react-icons/vsc";

const Roadmap = ({ ClassSpanTitle }) => {
  const data = [
    {
      month: "",
      year: "2021",
      list1: "WIZZ STARTED TO PLAN TO INTRODUCE THE DEFI WIZZ ECOSYSTEM",
      list2: "STARTED THE IDEA DEVELOPMENT",
      list3: "DEAL WITH COMPLIANCE",
      class: "date-content date-content1",
    },
    {
      month: "Q2",
      year: "2022",
      list1: "HIRE EXPERTS IN THE INDUSTRY, START DEVELOPING FROM THE SCREECH",
      list2: "DEVELOPMENT OF NEW UI/UX",
      list3: "STARTED TO PROGRAMME AND DESIGN",
      class: "date-content date-content2",
    },
    {
      month: "Q4",
      year: "2022",
      list1: "LAUNCH OF THE WEBSITE",
      list2: "START TO SELL NODE",
      list3: "ONBOARD MARKETING TEAM",
      list4: "SOCIAL MEDIA MARKETING",
      list5: "MARKETING CAMPAIGN",
      list6: "SMART CONTRACT DEVELOPMENT",
      class: "date-content date-content3",
    },
    {
      month: "Q1",
      year: "2023",
      list1: "WIZZ TOKEN WHITEPAPER",
      list2: "DEX LISTING",
      list3: "PARTNERSHIP DEVELOPMENT",
      list4: "POWER NODE NFT AIRDROPS",

      class: "date-content date-content4",
    },

    {
      month: "Q2",
      year: "2023",
      list1: "PARTNERSHIP ANNOUNCEMENT",
      list2: "ON-CHAIN INTEGRATION DEVELOPMENT",
      list3: "CENTRALISE EXCHANGE LISTING",
      list4: "DESIGN & DEVELOPMENT OF NODE NFT MARKETPLACE",
      class: "date-content date-content5",
    },
    {
      month: "MID",
      year: "2023",
      list1: "MORE TO BE ANNOUNCED",
      class: "date-content date-content6",
    },
  ];

  return (
    <section
      className="roadmap section-padding-100-0  mt-md-0 mt-5 "
      id="roadmap"
    >
      <SectionHeading
        title="Our ICO Roadmap"
        // text="Our ICO Roadmap"
        ClassSpanTitle={ClassSpanTitle}
      />
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="main-timeline">
              {data?.map((item, key) => (
                <div className="timeline" key={key}>
                  <div className="icon" />
                  <div className={item.class}>
                    <div className="date-outer">
                      <span className="date">
                        <span className="month">{item.month}</span>
                        <span className="year">{item.year}</span>
                      </span>
                    </div>
                  </div>
                  <div className="timeline-content text-light">
                    <h5 className="title">
                      <li>{item.title}</li>
                    </h5>
                    {/* <p className="description text-light-gray">{item.description}</p> */}
                    {/* <ul> */}
                    {item.list1 && <li>{item.list1}</li>}
                    {item.list2 && <li>{item.list2}</li>}
                    {item.list3 && <li>{item.list3}</li>}
                    {item.list4 && <li>{item.list4}</li>}
                    {item.list5 && <li>{item.list5}</li>}
                    {item.list6 && <li>{item.list6}</li>}
                    {item.list7 && <li>{item.list7}</li>}
                    {item.list8 && <li>{item.list8}</li>}
                    {item.list9 && <li>{item.list9}</li>}
                    {/* </ul> */}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      <div id="contactUs"></div>
      <div className="section-padding-0-100"></div>
    </section>
  );
};

export default Roadmap;
